.home-root {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.home-container {
  position: relative;
  min-height: 100vh;
  display: flex;
  flex-direction: row;
  /* <-- row, not column */
  align-items: stretch;
  /* <-- stretch, not center */
}

.home-topbar {
  width: 100%;
  height: 64px;
  background: #222;
  display: flex;
  align-items: center;
  padding: 0 0 0 0.5rem;
  /* <-- Less left padding, keep right padding if needed */
  box-sizing: border-box;
  position: relative;
  z-index: 2;
}

.home-logo {
  height: 48px;
  width: auto;
  margin-right: 1rem;
}

.home-main {
  display: flex;
  flex: 1;
  min-height: 0;
}

.home-sidebar {
  min-width: 220px;
  background: white;
  padding: 2rem 1rem;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  box-shadow: 2px 0 8px rgba(0, 0, 0, 0.05);
}

.home-content {
  flex: 1;
  padding: 2rem 2rem 2rem 3rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.home-top-right {
  position: absolute;
  top: 20px;
  right: 20px;
  display: flex;
  gap: 10px;
}

.home-top-right span {
  color: white;
  font-size: 14px;
  font-weight: bold;
}